* {
  box-sizing: border-box;
}

.body {
  flex-direction: row;
  justify-content: center;
  display: flex;
  position: relative;
}

.form-container {
  max-width: 100%;
  background-color: #2196f3;
  margin: auto;
}

.search-form {
  max-width: 300px;
  margin: auto;
}

.input {
  float: left;
  width: 80%;
  background: #fff;
  border: 1px solid gray;
  padding: 10px;
  font-size: 17px;
}

.formBtn {
  float: left;
  width: 20%;
  color: #fff;
  cursor: pointer;
  background: #f1f1f1;
  border: 1px solid gray;
  border-left: none;
  padding: 10px;
  font-size: 17px;
}

.formBtn:hover {
  background: #0b7dda;
}

.search-form:after {
  content: "";
  clear: both;
  display: table;
}

.load-more {
  width: 100px;
  height: 30px;
  position: fixed;
  bottom: 0;
  right: 50%;
}

.load-more:hover {
  color: #fff;
  background-color: #0b7dda;
}

.loadMoreHidden {
  display: none;
}

.loadMoreVisible {
  display: block;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 80%;
  grid-gap: 12px;
  flex-wrap: wrap;
  grid-template-columns: repeat(auto-fit, minmax(17%, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-top: 1%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__item {
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 180px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  max-width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item:not(:last-child) {
  margin-right: 3%;
}

.info-item b {
  color: #000;
  font-size: 13px;
}

a {
  color: #8d8176;
  border: none !important;
  text-decoration: none !important;
}

#container {
  width: 200px;
  height: 200px;
  margin: 20px;
  position: relative;
}

